@import url('https://fonts.googleapis.com/css2?family=Black+Han+Sans&family=Montserrat:wght@500;700&display=swap');

.App {
  text-align: left;
}

.title {
  font-family: 'Black Han Sans', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 120px;
  line-height: 120%;
  display: flex;
  align-items: center;
  letter-spacing: 0.14em;

  color: #FFFF00;

  text-shadow: 10px 10px 6px #6100FF, -2px 0 #6100FF, 0 2px #6100FF, 2px 0 #6100FF, 0 -2px #6100FF;
  
  transform: matrix(0.99, 0, -0.12, 1, 0, 0);
  white-space: pre-wrap;
}

.titleKO {
  font-family: 'Black Han Sans';
  font-style: normal;
  font-weight: 400;
  font-size: 120px;
  line-height: 120%;
  /* identical to box height */

  text-align: right;

  color: #FFFFFF;

  text-shadow: 8px 8px 6px #6100FF, -2px 0 #6100FF, 0 2px #6100FF, 2px 0 #6100FF, 0 -2px #6100FF;
  transform: matrix(0.99, 0, -0.12, 1, 0, 0);
}

.instruction {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 140.1%;
  white-space: pre-wrap;
}

.dday {
  font-family: 'Gulim';
  font-style: normal;
  font-weight: 400;
  font-size: 35px;
  line-height: 40px;
  text-align: right;
  letter-spacing: -0.12em;

  color: #FFFFFF;

  text-shadow: 5px 5px 6px #6100FF;
  filter: blur(1px);

  position: fixed;
  top: 50px;
  right: 100px;
}